.project-card {
  padding: 1rem;
  text-align: center;
  width: 100%; /* Full width on large screens */
  flex: none;
  max-width: 1200px; /* Adjust as needed */
  margin: auto; /* Center the card if max-width is used */
}

.project-media img,
.project-media iframe {
  max-width: 100%; /* Ensures media does not exceed the container's width */
  height: auto; /* Maintains aspect ratio */
  border-radius: 2%;
  transition: opacity 0.5s ease; /* Smooth transition for media */
}

.project-media {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  height: 0;
  overflow: hidden;
}

.project-media iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.5s ease;
}

.project-video {
  opacity: 0;
  transition: opacity 0.5s ease;
}

.project-video.show-video {
  opacity: 1;
}

.project-card h3 {
  margin-top: 1em;
  color: var(--purple-text);
  transition: color 0.3s ease;
}

.project-card p {
  font-size: 1rem;
  margin-top: 0.5em;
}

.project-card a {
  display: inline-block;
  margin: 1rem;
  margin-left: 0.5rem;
  color: var(--purple-text);
  background-color: var(--comments);
  padding: 0.5em 1em;
  border-radius: 4px;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.project-card a:hover {
  background-color: #3b405a;
}

.project-card img:hover {
  opacity: 0.3;
}

