.about-me {
    color: var(--general-text);
    padding: 11.5rem 0;
  }

  .h2-container {
    margin-left: 2%;
    margin-bottom: 5%;
  }

  h2 {
    font-size: 2.5rem;
    font-weight: normal;
  }

  .about-image img {
    max-width: 70%;
    height: auto;
    border-radius: 50%; /* Circular image */
    margin: 0px 0px 0px 20%;
  }

  .social-links {
    display: flex;
    justify-content: center; /* Center the icons horizontally */
    align-items: center; /* Align the icons vertically */
    gap: 10px; /* Space between the icons */
    margin-top: 1rem; /* Space below the profile picture */
    margin-left: 7%;
  }
  
  .social-icon {
    width: 2.5em; /* Adjust the size as needed */
    height: auto;
    /* If you want to add any hover effects or additional styling, add them here */
  }
  
  .about-me .container {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .about-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .about-text {
    max-width: 95%;
    margin: 1.5rem;
  }

  .about-p {
    margin-top: 2%;
  }
  
  /* Adjusted styles for .skills-list */
  .skills-list {
    list-style-type: none;
    display: grid; /* Switch to grid */
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr)); /* Adjust '120px' to your preference */
    gap: 1rem;
    padding: 0;
  }
  
  .skills-list li {
    color: var(--general-text);
    font-size: 1rem; /* Adjust the font size for readability */
    background: var(--comments);
    padding: 5px 10px;
    border-radius: 1px;
    text-align: center;
    /* font-weight: bold; */
    margin-bottom: 0.5rem; /* Space below each item for when they wrap */
    white-space: nowrap; /* Prevents the text inside from wrapping */
  }

  .btn {
    display: inline-block;
    background-color: var(--purple-text);
    border-radius: 4px;
    color: var(--background-color);
    padding: 5px 15px;
    margin: 2rem 1rem 0 0;
    text-decoration: none;
    transition: background-color 0.5s ease;
    /* transition: color 0.1s ease; */
  }

  .btn:hover {
    background-color: var(--comments);
    color: var(--general-text);
  }

  section::after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 200px;
    background: linear-gradient(to top, #130F1B, transparent);
  }
  
  /* Responsive styles */
  @media (max-width: 1350px) {
    .about-me .container, .about-content {
      flex-direction: column;
    }

    .h2-container {
      margin-left: 0;
      margin-bottom: 0;
    }
  
    .about-text {
      max-width: 100%;
      margin-bottom: 2rem; /* Space between text and image */
    }

    .about-image{
      max-width: 30%;
      min-width: 25%;
      margin: 5%;
    }

    .skills-list li {
      font-size: 0.9rem; /* Slightly smaller font size for very small screens */
      padding: 5px; /* Less padding on smaller screens */
    }

    .skills-list {
      grid-template-columns: repeat(auto-fit, minmax(75px, 1fr)); /* Larger min-width for bigger screens */
    }

    @media (min-width: 768px) {
      .about-me {
        padding: 4rem 0; /* Fixed margin for larger screens */
      }
    }
  
}
  