.project-section {
  padding: 4rem 0;
  background: linear-gradient(to top, #130F1B, transparent);
  color: var(--general-text);
  display: flex; 
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

/* This will be the visible window */
.scroller {
  overflow: hidden;
  width: 60%;
}

/* This will contain all project cards side by side */
.carousel-container {
  display: flex;
  flex-wrap: nowrap; /* Prevents the cards from wrapping */
  transition: transform 0.5s ease; /* Smooth transition for the sliding effect */
}


/* Buttons for navigation */
.project-section button {
  background-color: transparent;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  z-index: 1;
  margin: 0 2vw; /* This will use 2% of the viewport width */
  transition: transform 0.1s, box-shadow 0.1s; /* Smooth transition for visual feedback */
}

.project-section button:active {
  transform: translateY(2px); /* Slightly move the button down to mimic a press */
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2); /* Optional: add shadow to give depth */
}

@media (min-width: 768px) {
  .project-section button {
    margin: 0 20px; /* Fixed margin for larger screens */
  }
}

@media (min-width: 1200px) and (max-width: 1500px) {
  .project-section {
    height: 110vh;
  }
}

